import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"
import poster from "../files/davidjlee_poster.pdf"
import resume from "../files/davidjlee_resume.pdf"
import styles from "./index.module.css"

const Section = ({ title, children }) => (
  <div className={styles.section}>
    <h2 className={styles.title}>{title}</h2>
    <ul className={styles.items}>
      {
        React.Children.map(children, (child, i) => (
          <li className={styles.item}>
            {child}
          </li>
        ))
      }
    </ul>
  </div>
)

const OutLink = ({ to, children, className }) => (
  <a 
    href={to}
    className={className}
    target="_blank" 
    rel="noopener noreferrer">
    {children}
  </a>
)

const Sep = ({ sep }) => (<span style={{ margin: '0 .5rem', }}>{sep}</span>)

const Project = ({ to, name, language, children }) => {
  
  const Title = () => (
    to ?
      <OutLink className={styles.title} to={to}>{name}</OutLink>
      :
      <span className={styles.title}>{name}</span>
  )
  
  return (
    <div className={styles.project}>
      <div className={styles.heading}>
        <Title />
        <Sep />
        <span className={styles.lang}>{language}</span>
      </div>
      <div className={styles.description}>{children}</div>
    </div>
  )
}

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <div className={styles.main}>
      <h1 className={styles.siteTitle}>David J. Lee</h1>
      <p className={styles.bio}>
        I'm a computer science / math major at {" "}
        <OutLink to="https://www.williams.edu">Williams College</OutLink>.
        My preferred languages are Clojure, Scala, Swift, and Python.
      </p>
      <Section title='Links'>
        <OutLink to={resume}>Résumé</OutLink>
        <a href="mailto:djl5@williams.edu">Email</a>
        <OutLink to="https://github.com/deejayessel">GitHub</OutLink>
      </Section>
      <Section title='Projects'>
        <Project 
          name='Firestone' 
          language='Clojure' 
          >
          A game engine for Blizzard's Hearthstone in Clojure.
          The core engine consists entirely of pure functions that are rigorously
          tested -- mutation is limited to the namespace handling the engine's
          interface with a web view.
        </Project>
        <Project 
          name='Augmented Reality Drawing' 
          language='Swift' 
          to="https://github.com/deejayessel/ar-drawing"
          > 
          An AR drawing environment for iOS devices.  
          Tracks device position using ARKit 2 to allow users to draw curves in 3-D space
          by moving their devices.
        </Project>
        <Project
          name='Blog Development'
          language='ReactJS, GatsbyJS' 
          to="https://maki-yoshimura.netlify.com"
          >
          A blog for Japanese students applying to US colleges.  
          Designed from scratch, built using React and Gatsby, and hosted on Netlify.
          Responsive.
          Source available <OutLink to="https://github.com/deejayessel/maki-blog">here</OutLink>.
        </Project>
      </Section>
    <Section title='Research'>
      <div className={styles.research}>
        <div className={styles.heading}>
          <OutLink className={styles.title}
                   to={poster}>
            Inferring Synchronization Disciplines to Verify Atomicity of Concurrent Code [poster, 2019]
          </OutLink>
        </div>
        <div className={styles.description}>
          w/ {' '}
          <OutLink to="http://dept.cs.williams.edu/~freund/index.html">
            Prof. Stephen Freund
          </OutLink>
          {' '} as part of {' '}
          <OutLink to="http://www.cs.williams.edu/~freund/synchronicity/">Synchronicity</OutLink>.
        </div>
      </div>
    </Section>

    </div>
    </Layout>
)

export default IndexPage
